<template>
  <div style="padding: 2.5rem 0">
    <WriteView />
    <TableView />

    <nav aria-label="Page fixed-bottom navigation example">
      <ul class="pagination">
        <li class="page-item" v-if="this.$route.params.id > 10">
          <a class="page-link">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>

        <li
          class="page-item"
          :class="{ active: i == this.$route.params.id }"
          v-for="i in parseInt(pageSize / 10) + 1"
          :key="i"
        >
          <a
            class="page-link"
            @click="
              this.$router.push({ name: communityTable, params: { id: i } })
            "
          >
            {{ i }}
          </a>
        </li>
        <li
          class="page-item"
          v-if="this.$route.params.id > parseInt(pageSize / 10) + 1"
        >
          <a class="page-link">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
import TableView from "./CommunityTableView.vue";
import WriteView from "./CommunityWriteView.vue";
import axios from "axios";

export default {
  name: "CommunityView",
  data() {
    return {
      pageSize: "",
      isActive: 1,
    };
  },
  components: { TableView, WriteView },
  mounted() {
    axios("/api/posts", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("jwt")}`,
      },
    }).then((res) => {
      this.pageSize = res.data.meta.pagination.total;
    });
  },
  created() {},
};
</script>
<style>
.pagination {
  justify-content: center;
}
.page-link {
  background-color: black !important;
}
.page-link > span {
  color: #000;
}
</style>
