<template>
  <div v-if="DetailShow" class="board_detail_view">
    <table class="table table-dark">
      <thead>
        <tr>
          <th scope="col">id</th>
          <th scope="col">제목</th>
          <th scope="col">날짜</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{{ detailPost.id }}</td>
          <td>{{ detailPost.attributes.title }}</td>
          <td>
            {{ detailPost.attributes.createdAt[0] }}
            <br />
            {{ detailPost.attributes.createdAt[1] }}
          </td>
          <td>
            <button
              type="button"
              class="btn btn-dark"
              @click="this.DetailShow = false"
            >
              X
            </button>
          </td>
        </tr>
      </tbody>
      <div class="tableContent">
        {{ detailPost.attributes.content }}
      </div>
    </table>
  </div>
  <div style="padding: 0rem 1rem">
    <div class="row justify-content-around table-top-btn">
      <div class="col-4">
        <button
          type="button"
          class="btn btn-dark"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
          data-bs-whatever="@mdo"
        >
          글쓰기
        </button>
      </div>
      <div class="col-4">
        <button type="button" class="btn btn-dark">내 글</button>
      </div>
      <div class="col-4">
        <button type="button" class="btn btn-dark">내 댓글</button>
      </div>
    </div>
    <table class="table table-dark" style="margin-bottom: 0">
      <tr class="table-dark" style="">
        <td class="table-dark">아이디</td>
        <td class="table-dark">제목</td>
        <td class="table-dark">날짜</td>
      </tr>
      <tr
        class="table-dark"
        v-for="i in posts"
        :key="i"
        @click="CommunityDetailView(i.id)"
      >
        <td class="table-dark">{{ i.id }}</td>
        <td class="table-dark">{{ i.title }}</td>
        <td class="table-dark">
          {{ i.createdAt[0] }} <br />
          {{ i.createdAt[1] }}
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
import axios from "axios";
import moment from "moment";

export default {
  name: "TableView",
  data() {
    return {
      DetailShow: false,
      posts: [],
      detailPost: {},
    };
  },
  methods: {
    CommunityDetailView(id) {
      axios(`/api/posts/${id}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("jwt")}`,
        },
      })
        .then((res) => {
          let date = res.data.data.attributes.createdAt;
          date = moment(moment(date).add("9", "h")).format(
            "YYYY년 M월 DD일" + "." + "HH시 MM분 ss초 A"
          );
          this.detailPost = res.data.data;
          console.log("date", date);
          this.detailPost.attributes.createdAt = date.split(".");
          this.DetailShow = true;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    mountedGetBoard(id) {
      axios(`/api/posts?pagination%5Bpage%5D=${id}&sort[0]=id%3Adesc`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("jwt")}`,
        },
      })
        .then((res) => {
          let data = res.data.data;
          let post = [];
          for (let i = 0; i < data.length; i++) {
            let date = data[i].attributes.createdAt;
            date = moment(moment(date).add("9", "h")).format(
              "YYYY년 M월 DD일" + "." + "HH시 MM분 ss초 A"
            );
            post.push({
              id: data[i].id,
              title: data[i].attributes.title,
              createdAt: date.split("."),
            });
          }
          this.posts = post;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.mountedGetBoard(this.$route.params.id);
  },
  computed: {
    thisPage() {
      return this.$route.params.id;
    },
  },
  watch: {
    thisPage() {
      axios(
        `/api/posts?pagination%5Bpage%5D=${this.$route.params.id}&sort[0]=id%3Adesc`,
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("jwt")}`,
          },
        }
      ).then((res) => {
        let data = res.data.data;
        let post = [];
        for (let i = 0; i < data.length; i++) {
          let date = data[i].attributes.createdAt;
          date = moment(moment(date).add("9", "h")).format(
            "YYYY년 M월 DD일" + "." + "HH시 MM분 SS초"
          );
          post.push({
            id: data[i].id,
            title: data[i].attributes.title,
            createdAt: date.split("."),
          });
        }
        this.posts = post;
        return res.data.meta.pagination.total;
      });
    },
  },
};
</script>
